.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background: url(../images/star.svg) no-repeat;
    background-position: center;
    margin-right: 4px;
  }

  &__star:last-child {
    margin-right: 0;
  }

  @for $starsNum from 1 through 5 {
    &--#{$starsNum} > .stars__star:nth-child(-n + #{$starsNum}) {
      background: url(../images/star-active.svg) no-repeat;
      background-position: center;
    }
  }
}
